.navigation__toggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.navigation__toggle:active {
  background-color: #f0ebff;
}
.navigation__toggle::before, .navigation__toggle::after {
  content: "";
  position: absolute;
  left: 22px;
  width: 16px;
  height: 2px;
  background-color: #000000;
}
.navigation__toggle::before {
  top: 24px;
  box-shadow: 0 5px 0 0 #000000;
}
.navigation__toggle::after {
  bottom: 24px;
}
.no-js .navigation__toggle {
  display: none;
}
.is-open .navigation__toggle::before {
  top: 24px;
  box-shadow: none;
  transform: translateY(5px) rotate(-45deg);
}
.is-open .navigation__toggle::after {
  bottom: 24px;
  transform: translateY(-5px) rotate(45deg);
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

